:host {
  --line-color: #9abec0;
  --pre-title-color: #9a999e;
  --title-color: #455a5a;
  --subtitle-color: #607b7a;

  display: block;
}

.mt-title-h1 {
  display: flex;
  flex-direction: column;
  padding-left: 12px;
  border-left: 12px solid var(--line-color);

  &__pre-title,
  &__title,
  &__subtitle {
    margin: 0;
    text-transform: uppercase;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    line-height: 1;
  }

  &__pre-title {
    font-size: 16px;
    color: var(--pre-title-color);
  }

  &__title {
    margin: 4px 0;
    font-weight: 600;
    font-size: 40px;
    color: var(--title-color);
  }

  &__subtitle {
    font-size: 24px;
    color: var(--subtitle-color);
  }
}
